'use client'

import { useEffect, useState } from 'react';
import { useParams } from 'next/navigation';  // Asegúrate de usar el hook `useParams` de Next.js
import { getActorDetails, getActorMovies } from '@/lib/tmdb'; // Asegúrate de tener estas funciones implementadas

// Componente para mostrar los detalles de un actor
export default function ActorDetailsPage() {
  const { id } = useParams(); // Obtener el ID del actor desde la URL
  const [actorDetails, setActorDetails] = useState(null);
  const [actorMovies, setActorMovies] = useState([]);

  useEffect(() => {
    const fetchActorDetails = async () => {
      try {
        // Obtener detalles del actor
        const details = await getActorDetails(id);
        setActorDetails(details);

        // Obtener la filmografía del actor
        const movies = await getActorMovies(id);
        setActorMovies(movies.cast || []);  // Usamos `cast` en caso de que los resultados vengan en esa clave
      } catch (error) {
        console.error('Error fetching actor details:', error);
      }
    };

    if (id) {
      fetchActorDetails();
    }
  }, [id]);

  if (!actorDetails) return <div>Loading...</div>; // Cargar datos antes de mostrar la vista

  return (
    <div className="p-8 max-w-6xl mx-auto text-white">
      {/* Contenedor de detalles del actor */}
      <div className="flex flex-col lg:flex-row gap-8 mb-12">
        {/* Imagen del actor */}
        <div className="w-full lg:w-1/3 max-w-sm flex flex-col gap-4">
          {actorDetails.profile_path && (
            <img 
              src={`https://image.tmdb.org/t/p/w500${actorDetails.profile_path}`} 
              alt={actorDetails.name} 
              className="rounded-lg shadow-lg w-full h-auto object-cover"
            />
          )}
        </div>

        {/* Información del actor */}
        <div className="flex-1 flex flex-col gap-6">
          <h1 className="text-4xl font-bold mb-4">{actorDetails.name}</h1>

          <div className="mb-8">
            <h2 className="text-2xl font-semibold mb-2">Biografía</h2>
            <p className="text-gray-300">{actorDetails.biography || 'Sin biografía disponible.'}</p>
          </div>

          <div className="text-xs bg-gray-900 rounded-lg p-4 shadow-md text-gray-300">
            {actorDetails.imdb_id && (
              <p>
                <strong>Enlace IMDb:</strong> 
                <a href={`https://www.imdb.com/name/${actorDetails.imdb_id}`} target="_blank" className="underline text-blue-400">
                  Ver en IMDb
                </a>
              </p>
            )}
          </div>
        </div>
      </div>

      {/* Filmografía del actor */}
      {actorMovies.length > 0 && (
        <div className="mt-12">
          <h2 className="text-2xl font-bold text-white mb-4">Filmografía</h2>
          <div className="grid grid-cols-4 gap-4">
            {actorMovies.map(movie => (
              <div key={movie.id} className="bg-gray-800 hover:bg-gray-700 rounded-lg">
                <img 
                  src={`https://image.tmdb.org/t/p/w300${movie.poster_path}`} 
                  alt={movie.title} 
                  className="w-full h-60 object-cover rounded-t-lg mb-2"
                />
                <p className="text-sm text-white truncate">{movie.title}</p>
              </div>
            ))}
          </div>
        </div>
      )}
    </div>
  );
}
